<template>
    <v-container class="pa-0" style="position: relative; width: 100vw" align="center">
        <div v-if="isEventLoaded">
            <v-btn class="top-left-fixed-button" text icon color="#FFFFFF"
                   @click="$router.go(-1)">
                <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-row class="ma-0" style="width: 100%">
                <v-img
                        v-if="headerImage"
                        class="white--text align-end"
                        :src="headerImage"
                        style="overflow: visible; margin-bottom: 26px; height: 150px; min-width: 100%"
                >
                    <v-chip
                            class="centered-event-name"
                            color="primary"
                            block
                            x-large
                    >
                        <strong>{{ name }}</strong>&nbsp;
                    </v-chip>
                </v-img>
            </v-row>
            <v-row class="ma-0" style="height: 100%; padding: 24px 24px 10px 24px">
                <div>
                    <p align="center">"{{description}}"</p>

                    <p align="center">{{date}}</p>

                    <p align="center" class="text-with-icon">
                        <v-icon style="display: inline;  vertical-align: top; font-size:18px;">mdi-store</v-icon>
                        {{address}}
                    </p>

                    <p align="center" class="text-with-icon">
                        <v-icon style="display: inline;  vertical-align: top; font-size:18px;">mdi-map-marker-outline
                        </v-icon>
                        {{distance}} km
                    </p>
                </div>
            </v-row>
            <v-row class="ma-0" style="width: 100%; height: 60%; margin-bottom: 64px !important;">
                <gmap-map
                        v-if="location"
                        :center="location"
                        :zoom="18"
                        style="width:100%;  height: 100%; position: absolute;"
                >
                    <gmap-marker
                            :position="location"
                    ></gmap-marker>
                </gmap-map>
            </v-row>
        </div>
    </v-container>
</template>

<script>
    import {mapActions, mapState, mapGetters, mapMutations} from 'vuex';

    export default {
        name: "EventDescription",
        data() {
            return {
                headerImage: null,
                location: null,
                name: '',
                description: '',
                date: null,
                address: '',
                distance: null,
                isEventLoaded: false
            }
        },
        methods: {
            ...mapActions({
                getEventFirebase: 'getEventFirebase',
            }),
            setEventInfo(event) {
                this.headerImage = event.headerImage;
                this.location = {
                    lat: event.coordinates.latitude,
                    lng: event.coordinates.longitude
                };
                this.name = event.name;
                this.description = event.description;
                this.date = this.$store.getters.getFormattedDateInTextMonth(event.date.toDate());
                this.address = event.address;
                this.distance = event.distance;
            }
        },
        computed: {},
        mounted() {
            let self = this;
            let eventID = self.$route.params.id;
            let event = self.$store.getters.getEvent(eventID);

            if (null == event) {
                self.getEventFirebase(eventID).then(function (event2) {
                    self.setEventInfo(event2);
                    self.isEventLoaded = true;
                })
                    .catch(function (error) {
                        console.log(error);
                    });
            } else {
                self.setEventInfo(event);
                self.isEventLoaded = true;
            }


        }

    }
</script>

<style scoped>
    .centered-event-name {
        position: absolute;
        top: 100%;
        left: 50%;
        padding: 10px;
        height: auto;
        transform: translate(-50%, -50%);
    }

    .top-left-fixed-button {
        position: fixed;
        top: 0px !important;
        left: 0px !important;
        z-index: 2;
    }
</style>
